"use client";

import Image from "next/image";
import { useState } from "react";

export default function Home() {
  const [file, setFile] = useState<File | null>(null);
  const [videoPreviewUrl, setVideoPreviewUrl] = useState<string | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const selectedFile = e.target.files[0];
      setFile(selectedFile);

      if (selectedFile.type.startsWith("video/")) {
        setVideoPreviewUrl(URL.createObjectURL(selectedFile));
      } else {
        setVideoPreviewUrl(null);
        console.error("Selected file is not a video.");
      }
    }
  };

  const handleUpload = async () => {
    if (!file) {
      console.error("No file selected.");
      return;
    }

    const formData = new FormData();
    formData.append("video", file);

    try {
      const response = await fetch("/api/uploadVideo", {
        method: "POST",
        body: formData,
      });

      if (response.ok) {
        console.log("Video uploaded successfully.");
      } else {
        console.error("Video upload failed.");
      }
    } catch (error) {
      console.error("Error uploading video:", error);
    }
  };

  return (
    <div className="bg-gray-900 text-gray-100 min-h-screen">
      {/* Header */}
      <header className="flex items-center justify-between p-4 border-b border-gray-800">
        <div className="flex items-center">
          <div className="w-10 h-10 bg-blue-500 rounded-full mr-4">
            {/* Replace with your logo */}
          </div>
          <span className="text-lg font-semibold">Heureseressttion</span>
        </div>
        <nav className="flex items-center space-x-4">
          <a href="#" className="hover:text-gray-300">
            Aricandrags
          </a>
          <a href="#" className="hover:text-gray-300">
            Inegative pichapsis
          </a>
          <a href="#" className="hover:text-gray-300">
            Hrvatings
          </a>
          <a href="#" className="hover:text-gray-300">
            Show By Vince
          </a>
        </nav>
      </header>

      <main className="flex flex-col items-center p-8">
        {/* Video Preview/Upload Area */}
        <div className="w-full max-w-2xl mb-8 p-6 bg-gray-800 rounded-lg shadow-md">
          {videoPreviewUrl ? (
            <video
              src={videoPreviewUrl}
              controls
              className="w-full rounded-lg"
            />
          ) : (
            <div className="flex flex-col items-center justify-center h-64 border-2 border-dashed border-gray-700 rounded-lg">
              <input
                id="file"
                type="file"
                accept="video/*"
                onChange={handleFileChange}
                className="hidden"
              />
              <label
                htmlFor="file"
                className="cursor-pointer bg-blue-500 hover:bg-blue-600 text-white font-semibold py-2 px-4 rounded-lg"
              >
                Upload File
              </label>
            </div>
          )}
        </div>

        {/* File Details */}
        {file && (
          <section className="mb-8 text-center">
            <h2 className="text-lg font-semibold mb-2">File Details:</h2>
            <ul className="list-disc list-inside">
              <li>Name: {file.name}</li>
              <li>Type: {file.type}</li>
              <li>Size: {file.size} bytes</li>
            </ul>
          </section>
        )}

        {/* Analyze Button */}
        {file && (
          <button
            onClick={handleUpload}
            className="bg-green-500 hover:bg-green-600 text-white font-semibold py-2 px-4 rounded-lg"
          >
            Analyze Video
          </button>
        )}

        {/* Description */}
        <p className="mt-8 text-center text-sm text-gray-400">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Tempor
          capacious herm/golf, aptus fabricentur opt kemens tool. Amallyus
          lussupturd tortion stgest. Ut himsa at hadigan loneapta old them
          accher rothor or irant tod-Burs mts oft ve uciis iguss lolasz sit.
        </p>
      </main>

      {/* Footer */}
      <footer className="p-4 text-center text-sm text-gray-500">
        <p>&copy; 2024 Your Company. All rights reserved.</p>
      </footer>
    </div>
  );
}